.FinishedQuiz{
  padding: 20px;
  color: #fff;
  border: 2px solid #ffffff;
  border-radius: 5px;
  box-sizing: border-box;
  margin: 0 10px;
    ul{
      list-style: none;
      padding: 0;
      margin: 0;
        li{
          i{
            margin-left: 10px;
          }
        }
    }
}

.error{
  color: indianred;
}
.success{
  color: aquamarine;
}